body {
  box-sizing: border-box;
  --header-active-color: #00acdc;
  font-family: Roboto, sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header-logo-link {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  letter-spacing: 0;
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 100%;
  display: flex;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: var(--header-active-color);
  cursor: pointer;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--header-active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
